@mixin component-transition {
  &-enter {
    opacity: 0;
    transform: scale(0.9);
    &-active {
      opacity: 1;
      transform: translateX(0);
      transition: opacity 300ms, transform 300ms;
    }
  }
  &-exit {
    opacity: 1;
    &-active {
      opacity: 0;
      transform: scale(0.9);
      transition: opacity 300ms, transform 300ms;
    }
  }
}
